export default {
	data() {
		return {
			nickname: '登陆/注册',
			head: '../../static/me/head.png'
		}
	},
	onLoad() { // 页面加载完成
		this.isLogin()
	},
	methods: {
		toHistory() { //历史记录
			uni.navigateTo({
				url: 'history'
			});
		},
		toCollect() { //我的收藏
			uni.navigateTo({
				url: 'collect'
			});
		},
		toUppass() { //修改密码
			uni.navigateTo({
				url: 'updpass'
			});
		},
		toFeedback() { //问题反馈
			uni.navigateTo({
				url: 'feedback'
			});
		},
		isLogin() { //判断登录状态
			const userinfo = uni.getStorageSync('userinfo');
			if (userinfo) {
				this.nickname = userinfo.nickname;
				if (userinfo.sex == 1) {
					this.head = '../../static/me/boy.png';
				}
				if (userinfo.sex == 2) {
					this.head = '../../static/me/girl.png';
				}
			} else {
				this.nickname = '登陆/注册';
				uni.navigateTo({
					url: '../login/login'
				});
			}
		},
		outLogin() { //退出登录
			uni.showModal({
				title: '提示',
				content: '确定退出登录',
				confirmColor:'#FF4400',
				success: function(res) {
					if (res.confirm) {
						// 清除账号缓存
						uni.removeStorageSync('userinfo');
						// 跳转到首页
						uni.reLaunch({
							url: '../index/index'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
}
